ul, ol, li {
    text-decoration: none;
    list-style: none;
    margin: 0;
    padding: 0;
}

.main-table{
    width: calc(100vw - 80px);
    display: flex;
    align-content: space-around;
    justify-content: space-around;
    flex-direction: column;
}

.op-table {
    width: calc(100% - 90px); 
    height: auto;
    background-color: white;
    box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.7);
    border-radius: 40px;
    margin: 20px 15px;
    padding: 30px;
}

li {
    width: 100%;
    box-shadow: 8px 2px 6px rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 8px;
}

span {
    text-align: center;
    width: 25%;
}

@media only screen and (min-width: 200px) and (max-width: 500px) {
    body {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    h1 {
        letter-spacing: 10.0px;
    }

    /* LIST */

    .main-table {
        flex-direction: column;
    }

    .op-table {
        width: calc(100% - 90px);
    }

    li{
        display: flex;
        flex-direction: column;
    }

    span {
        width: 100%;
    }

